const { createContext, useState } = require("react");

const AuthContext = createContext();

const AuthContextProvider = ({ children }) => {
  const [isAuth, setisAuth] = useState(false);
  const [token, setToken] = useState("User has not login");

  const toggleLogin = (data) => {
    fetch(`https://reqres.in/api/login`, {
      method: "POST",
      body: JSON.stringify(data),
      headers: {
        "Content-Type": "application/json"
      }
    })
      .then((res) => res.json())
      .then((res) => {
        if (res.token !== undefined) {
          setisAuth(true);
          setToken(res.token);
        } else {
          setToken(`Wrong Details : Please enter Correct Details`);
        }
      })
      .catch((err) => setisAuth(false));
  };

  const handleLogOut = () => {
    setisAuth(false);
    setToken("User has not login");
  };
  return (
    <AuthContext.Provider value={{ isAuth, handleLogOut, token, toggleLogin }}>
      {children}
    </AuthContext.Provider>
  );
};

export { AuthContext, AuthContextProvider };
