const { createContext, useState } = require("react");

const ThemeContext = createContext();

const themes = {
  light: {
    background: "#f2f2f2",
    color: "black",
    padding: "50px",
    paddingTop: "10px"
  },
  dark: {
    background: "#404040",
    color: "#e6e6e6",
    padding: "50px",
    paddingTop: "10px"
  }
};

const ThemeContextProvider = ({ children }) => {
  const [currTheme, setTheme] = useState("light");

  const toggleTheme = () => {
    setTheme(currTheme === "light" ? "dark" : "light");
  };

  return (
    <ThemeContext.Provider value={{ themes, currTheme, toggleTheme }}>
      {children}
    </ThemeContext.Provider>
  );
};

export { ThemeContext, ThemeContextProvider };
