import { useContext, useState } from "react";
import { AuthContext } from "../Context/AuthContext";

const Form = () => {
  const [userDetails, setUserDetails] = useState({ email: "", password: "" });

  const { isAuth, handleLogOut, toggleLogin } = useContext(AuthContext);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setUserDetails({ ...userDetails, [name]: value });
  };

  const handleClick = () => {
    toggleLogin(userDetails);
  };

  const { email, password } = userDetails;

  return (
    <div className="formContent">
      <h1>Auth Context</h1>
      {isAuth ? (
        ""
      ) : (
        <div>
          <div>
            <h3>for login Enter below details</h3>
            <h4>email: "eve.holt@reqres.in"</h4>
            <h4>password: "cityslicka"</h4>
            <br />
          </div>
          <input
            type="email"
            placeholder="email"
            name="email"
            value={email}
            onChange={handleChange}
          />
          <br />
          <input
            type="text"
            placeholder="password"
            name="password"
            value={password}
            onChange={handleChange}
          />
        </div>
      )}
      <button onClick={isAuth ? handleLogOut : handleClick}>
        {isAuth ? "Log Out" : "Login"}
      </button>
    </div>
  );
};

export { Form };
