import { useContext } from "react";
import { ThemeContext } from "../Context/ThemeContext";
import { Footer } from "./Footer";
import { Form } from "./Form";
import { Status } from "./Status";

function ThemeChanger({ child }) {
  const { themes, currTheme, toggleTheme } = useContext(ThemeContext);
  const styles = currTheme === "light" ? themes.light : themes.dark;

  const buttonstyle =
    currTheme === "light"
      ? { ...themes.light, padding: "10px" }
      : { ...themes.dark, padding: "10px" };

  return (
    <div style={styles}>
      <h1>Theme Context</h1>
      <button onClick={toggleTheme} style={buttonstyle}>
        Toggle Theme
      </button>
      <Form />
      <Status />
      <Footer />
    </div>
  );
}

export { ThemeChanger };
