import { useContext } from "react";
import { AuthContext } from "../Context/AuthContext";

const Status = () => {
  const { isAuth, token } = useContext(AuthContext);
  return (
    <div>
      <h1>
        {isAuth ? (
          <div>
            <h4>Login Successfull</h4>
            <h4>Token is ${token}</h4>
          </div>
        ) : (
          `${token}`
        )}
      </h1>
    </div>
  );
};

export { Status };
